section {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;
}

.section__title {
  width: 90%;
  margin: 20px 0px 50px;
  padding: 5px 50px;
  position: relative;
  color: #00bcbc;
  font-size: 2.5em;
  text-align: center;
  /* text-shadow: 0px 0px 1px #000; */
}

.section__title::after {
  content: "";
  display: block;
  width: 100%;
  height: 5px;
  position: absolute;
  bottom: -4px;
  left: 0px;
  border-radius: 25px;
  background-image: linear-gradient(135deg, #00f578 0, #0097ff);
  /* box-shadow: 0px 1px 4px #000a; */
}

.section__products,
.section__adds {
  --min-width: 375px;
  width: 90%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(var(--min-width), 1fr));
  gap: 50px;
}

.section__adds {
  --min-width: 150px;
  width: 80%;
  gap: 30px 25px;
}