.modal {
  width: 75%;
  min-height: 90vh;
  display: grid;
  margin: 0 auto;
  color: #fff;
  position: fixed;
  top: 5vh;
  left: 12.5%;
  z-index: 20;
  font-family: 'Roboto', sans-serif;
}

.bckModal {
  width: 100%;
  height: 100vh;
  border: none;
  outline: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: #111d;
  cursor: pointer;
}

.closeModal {
  padding: 5px 20px 5px 20px;
  border: 3px solid currentColor;
  border-radius: 10px 0px 0px 10px;
  outline: none;
  position: absolute;
  top: 10px;
  right: -15px;
  z-index: 20;
  font-weight: bold;
  font-size: 20px;
  color: #000;
  background: rgba(254, 35, 35);
  cursor: pointer;
  transition: color .2s;
}

.closeModal:hover {
  color: #fff;
}

.newForm {
  max-width: 500px;
  width: 100%;
  height: 100%;
  padding: 10px 30px;
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  background-color: #05629c;
  border: 3px solid #000;
  gap: 20px;
  place-self: center;
  z-index: 10;
}

.newProductForm__title {
  max-width: 80%;
  font-size: 1.5em;
  align-self: center;
}

form {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: space-evenly;
  gap: 10px;
  overflow-y: auto;
  padding-right: 5px;
}

label {
  font-size: 1.2em;
  grid-column: 1/span 2;
}

label.number {
  grid-column: auto;
}

input,
textarea {
  display: block;
  width: 100%;
  padding: 5px;
  margin: 5px 0px;
  border: none;
  border-radius: 5px;
  outline: none;
  font-size: 14px;
}

textarea {
  resize: none;
}

input[required="true"]::after {
  content: "*";
  display: block;
  width: 10px;
  color: firebrick;
}

input[type="file"] {
  padding: 0;
}

form>button {
  padding: 8px 25px;
  border: 2px solid currentColor;
  border-radius: 25px;
  grid-column: 1/span 2;
  place-self: center;
  color: #00D688;
  background: transparent;
  font-size: 1.2em;
  cursor: pointer;
  transition: color .2s, background .2s;
}

form>button:hover {
  color: #fff;
  background: #00D688;
}

form>p {
  grid-column: 1/span 2;
  place-self: center;
  text-align: center;
}