@import url('https://fonts.googleapis.com/css2?family=Lobster+Two:ital@0;1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap');

:root {
  --main-color: #12D688;
  --main-color-lg: #111;
  --main-color-dk: #127475;
  --changedk: #111;
  --changelg: #fff;
}

button {
  border: none;
  outline: none;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  user-select: none;
  text-transform: capitalize;
  font-family: 'Roboto', sans-serif;
}

/* body {
  --changedk: #e2e2e2;
  --changelg: #333;
} */

.admin-mode {
  position: relative;
  display: block;
  margin: 10px auto;
  width: 80px;
  height: 40px;
  background: #000;
  border-radius: 25px;
}

#admin-mode {
  display: none;
}

.admin-mode::after {
  content: "";
  display: block;
  width: 32px;
  height: 32px;
  border-radius: 25px;
  border: 4px solid #000;
  background: #f00;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(0);
  transition: transform .5s, background .5s;
}

#admin-mode:checked~.admin-mode::after {
  background: #0f0;
  transform: translateX(40px);
}

/* Admin */

.newIndex {
  min-width: 30px;
  padding: 5px 0;
  border: 2px solid #000;
  border-radius: 5px;
  font-size: 1em;
  font-weight: bold;
  text-align: center;
  position: absolute;
  top: 10px;
  left: 10px;
}

.newProduct {
  --bck: #12abd6;
  cursor: pointer;
  width: calc(100% - 16px);
  height: calc(100% - 16px);
  place-self: center;
}

.adds__add.newProduct {
  width: 134px;
  justify-content: flex-start;
  gap: 20px;
}

.products__product.newProduct::after,
.products__product.newProduct::after {
  display: none;
}

.newProduct,
.newProduct>span,
.newProduct::before {
  background: var(--bck);
  border: 2px dashed #fff;
  box-shadow: 0px 0px 0px 8px var(--bck);
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 5px;
  transition: background .4s, box-shadow .4s;
}

.newProduct__title {
  color: #fff;
  font-size: 1.6em;
  text-align: center;
}

.newProduct>svg {
  height: 40%;
}

.newProduct:hover {
  --bck: #05629c;
}

@keyframes move {
  from {
    transform: scale(1.07) translateX(0) translateY(0);
  }

  to {
    transform: scale(1.09) translateX(0) translateY(0);
  }
}

.fadeIn {
  animation: fadeIn .5s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fadeInDown {
  animation: fadeInDown .5s;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-100vh);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}


@media screen and (max-width:768px) {

  header {
    padding: 5px 30px;
  }

  header>figure {
    width: 60px;
    height: 60px;
  }

  .instagram {
    width: 50px;
    height: 50px;
  }

  header>h1 {
    font-size: 2.5em;
  }

  .newSection__title {
    font-size: 2rem;
    padding: 20px 40px;
  }

  .newSection__body {
    height: 120px;
  }

  .modal {
    width: 80%;
    left: 10%;
  }

  .section__products {
    width: 87%;
  }
}

@media screen and (max-width:426px) {

  ::-webkit-scrollbar {
    width: 0px !important;
  }

  header {
    padding: 5px 20px;
  }

  header>figure {
    width: 50px;
    height: 50px;
  }

  .instagram {
    width: 40px;
    height: 40px;
    padding: 7px;
  }

  header>h1 {
    font-size: 2em;
  }

  .product__title,
  .product__price {
    font-size: 16px;
  }

  .add__title {
    font-size: 14px;
  }

  .product__price {
    min-width: 50px;
  }

  .product__body {
    font-size: 14px;
  }

  .adds__add {
    width: 130px;
    height: 220px;
    padding: 5px;
  }

  .add__price {
    min-width: 50px;
    padding: 4px 4px;
    font-size: 17px;
  }

  .section__title {
    font-size: 26px;
  }

  .section__title::after {
    height: 3px;
  }

  .section__adds {
    width: 90%;
  }

  .section__products {
    width: 85%;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }

  .products__product {
    height: 110px;
  }

  .newSection__title {
    font-size: 1.5rem;
    padding: 10px 20px;
  }

  .newProduct__title {
    font-size: 1.4rem;
  }

  .newSection__body {
    height: 80px;
  }

  .modal {
    width: 90%;
    left: 5%;
  }

  .newForm {
    padding: 10px 15px;
  }

  .edit__btn,
  .delete__btn {
    width: 37px;
    height: 37px;
  }

  .product__btns {
    bottom: -27px;
    left: -12px;
  }

  .add__btns {
    top: 10px;
    right: -24px;
  }

  * {
    cursor: none !important;
  }
}

