.products__product,
.adds__add {
  margin: 0 auto;
  display: flex;
  align-items: center;
  position: relative;
  background: #fff;
  border: 2px solid #3d3d3d;
  box-shadow: 3px 2px 7px #0007;
}

.products__product {
  --hover-color: #129bb6;
  --border-color: #13dea8;
  max-width: 450px;
  width: 100%;
  height: 150px;
  background: #fff;
  border-radius: 15px;
}

.products__product::after {
  content: "";
  display: block;
  width: 70%;
  height: calc(100% + 16px);
  border: 2px solid #13D6a8;
  border-radius: 10px;
  position: absolute;
  bottom: -10px;
  right: -17px;
  z-index: -1;
}

.product__index,
.add__index {
  min-width: 30px;
  padding: 4px 0;
  border: 2px solid #000c;
  border-radius: 3px;
  position: absolute;
  top: -20px;
  left: -22px;
  background: #fff;
  text-align: center;
  font-weight: bold;
  opacity: .8;
  transition: opacity .3s;
}

.product__index:hover,
.add__index:hover {
  opacity: 0;
}

.doblez {
  width: 46px;
  height: 46px;
  position: absolute;
  right: -17px;
  top: -10px;
  border: 2px solid var(--border-color);
  border-radius: 0 0 0 10px;
  clip-path: polygon(-10px -10px, 60px 60px, -10px 60px);
  box-shadow:
    inset 12px -12px 0px #fff,
    inset 14px -14px 0px #0005,
    inset 40px -40px 0px #fff,
    -3px 3px 5px 1px #0005;
  cursor: pointer;
}

.product__info,
.product__info::before {
  border-radius: 0px 10px 0px 0px;
  position: absolute;
}

.product__info {
  width: 46px;
  height: 46px;
  right: -17px;
  top: -10px;
  border: 2px solid var(--border-color);
  background: var(--hover-color);
  cursor: pointer;
  clip-path: polygon(0 0, 46px 0, 46px 46px);
  transition: background .3s;
}

.product__info:hover {
  --hover-color: #018ca3;
}

.product__info::after {
  content: "";
  display: block;
  width: 150%;
  height: 2px;
  position: absolute;
  top: 25px;
  left: -7px;
  transform: rotateZ(45deg);
  background: var(--border-color);
  box-shadow: -10px -2px 5px #000;
  user-select: none;
}

.product__info svg {
  width: 30px;
  position: absolute;
  right: 2px;
  top: 2px;
  fill: var(--border-color);
}

.product__img,
.add__img {
  aspect-ratio: 1/1;
  object-fit: cover;
  object-position: center;
}

.product__img {
  height: calc(100% - 4px);
  aspect-ratio: 1/1;
  border-radius: 4px;
  object-fit: cover;
  object-position: center;
  position: relative;
  top: -20px;
  left: -20px;
  box-shadow:
    0px 0px 0px 2px #000c,
    4px 4px 10px 0px #0008;
  background-color: #fff;
}

.product__title,
.product__body {
  max-height: 80%;
  margin: 0px 35px 0px 0px;
  text-align: center;
  text-wrap: balance;
  font-size: 22px;
  color: var(--changedk);
  overflow-y: auto;
  flex-grow: 1;
}

.product__body {
  font-size: 16px;
}

::-webkit-scrollbar {
  width: 8px;
  background-color: #333;
}

::-webkit-scrollbar-thumb {
  background-color: #999;
  border: 2px solid #333;
}

.product__price {
  min-width: 60px;
  position: absolute;
  bottom: -10px;
  right: -17px;
  padding: 5px;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  color: #13D6a8;
  border: 2px solid currentColor;
  background-color: var(--changelg);
  border-radius: 16px 0px 8px 0px;
  box-shadow:
    -2px -2px 10px 1px #02020250;
}

.adds__add {
  width: 150px;
  height: 250px;
  padding: 10px;
  flex-flow: column;
  gap: 5px;
  border-radius: 5px;
  border: 2px solid #13D6a8;
}

.add__index {
  top: -2px;
  left: -2px;
  color: #13D6a8;
  border: 2px solid #13D6a8;
}

.add__img {
  width: 80%;
  border: 2px solid #000;
  border-radius: 50%;
}

.add__title {
  padding: 0px 2px;
  margin-bottom: 20px;
  color: var(--changedk);
  font-size: 16px;
  text-align: center;
  text-wrap: balance;
  overflow-y: auto;
}

.add__price {
  min-width: 60px;
  position: absolute;
  bottom: -15px;
  padding: 5px 7px;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  color: #13D6a8;
  background-color: var(--changelg);
  border: 3px solid #13D6a8;
  border-radius: 15px 15px 0 0;
  box-shadow: 0px 4px 10px #0008;
}


.product__btns {
  position: absolute;
  bottom: -27px;
  left: 0px;
  display: flex;
  gap: 10px;
}

.add__btns {
  position: absolute;
  top: 15px;
  right: -24px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.edit__btn,
.delete__btn {
  width: 40px;
  height: 40px;
  padding: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: 2px solid #000;
  border-radius: 5px;
  outline: none;
  position: relative;
}

.edit__btn {
  background-color: #ffa500;
}

.delete__btn {
  background-color: #ff0000;
}

.edit__btn>svg,
.delete__btn>svg {
  width: 80%;
  height: 100%;
  fill: #fff;
  transition: fill .2s;
}

.edit__btn:hover>svg,
.delete__btn:hover>svg {
  fill: #e2e2e2;
}